* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

#main-canvas {
  border: 1px solid black;
}

#toolbox {
  position: fixed;
  width: 30vw;
  height: 100vh;
  top: 1em;
  right: 1em;
  overflow-y: auto;
}

#misc-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

#prim-option-box {
  height: 20vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, 3em);
  grid-auto-rows: 3em;
  gap: 0.5em;
  overflow-y: auto;
  padding: 1px;
}

#prim-used-box {
  height: 20vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 3em;
  gap: 0.5em;
  overflow-y: auto;
}

.image-button {
  background-color: transparent;
  border: 1px solid black;
}

.image-button:hover {
  outline: 1px solid black;
}

.image-icon {
  max-width: 100%;
  max-height: 100%;
}

.prim-entry {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
  border: 1px solid black;
  padding: 0 1em;
}

.prim-delete {
  justify-self: end;
}

.hidden {
  display: none;
}

.invalid {
  background-color: red;
}

#controls-set {
  display: flex;
  flex-direction: column;
}

#fieldset-controls input[type="number"] {
  width: 6em;
}

.control-bar {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-auto-rows: 1.5em;
  align-items: center;
  padding: 0.1em;
}

.control-bar label {
  font-size: 0.9rem;
}

.control-bar.boolean label {
  grid-column: 1 / 3;
}